.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* затенение */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal-content {
    position: relative; /* ОБЯЗАТЕЛЬНО */
    background: white;
    border-radius: 16px;
    padding: 48px;
    width: 90%;
    max-width: 600px;
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
    text-align: center;
}

.modal-title {
    font-family: 'Playfair', serif;
    font-size: 65px;
    font-weight: 500;
    margin-bottom: 40px;
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.modal-form input {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    font-size: 14px;
    padding: 12px 16px;
    border: 1px solid #000;
    border-radius: 4px;
}

.modal-form button {
    font-family: 'Manrope', sans-serif;
    font-size: 20px;
    background-color: #E3FA95;
    color: black;
    padding: 14px 32px;
    border: none;
    cursor: pointer;
    border-radius: 4px;
}

.modal-policy {
    margin-top: 24px;
    font-family: 'Playfair', serif;
    font-style: italic;
    font-size: 14px;
}

.modal-policy a {
    color: #750036;
    text-decoration: none;
}

.modal-confirmation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Playfair', serif;
    font-size: 24px;
    font-weight: 300;
    text-align: center;
    padding: 40px 20px;
}

.modal-confirmation svg {
    padding: 4px;
}

.checkmark {
    margin-bottom: 20px;
    overflow: visible;
}

.modal-close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    font-size: 24px;
    font-weight: bold;
    background: transparent;
    border: none;
    color: #000;
    cursor: pointer;
    z-index: 10;
    transition: opacity 0.3s ease;
}


.modal-close-button:hover {
    opacity: 0.6;
}




